@import "../../styles/common";

.mynft-card-box {
  .block-box.jt-card {
    // transition: 0.5s;
    // padding: 0.2rem;
    // position: relative;
    // border-radius: 0.3rem;
    // background: transparent
    //   linear-gradient(180deg, #e2ff00 0%, #18e0e0 47%, #e8318d 100%) 0% 0%
    //   no-repeat padding-box;
    // &::before {
    //   content: "";
    //   position: absolute;
    //   background: #000;
    //   top: 0.2rem;
    //   bottom: 0.2rem;
    //   left: 0.2rem;
    //   right: 0.2rem;
    //   z-index: 0;
    //   border-radius: 0.3rem;
    // }
    padding: 0.2rem;
    position: relative;
    border-radius: 0.5rem;
    transition: 0.5s;
    background: #212031;
    height: calc(100% - 2rem);
    margin: 1rem 0 !important;
    // box-shadow: 0.2rem 0.2rem 1.1rem #0e0e1c85, -0.2rem -0.2rem 1.1rem #35354585;
    &:hover {
      transform: translateY(-10px);
      transition: 0.5s;
    }
    .player_stats {
      position: absolute;
      top: 0.5rem;
      right: 0.5rem;
      z-index: 4;
      border-radius: 50rem;
      background: #000 0 0 no-repeat padding-box;
      border: 0.18rem solid #3b3b3b;
      .player-type {
        padding: 0.6rem 0.4rem;
        border-bottom: 0.1rem solid #3b3b3b;
        img {
          display: flex;
          max-height: unset !important;
          -o-object-fit: contain;
          object-fit: contain;
          -o-object-position: center;
          object-position: center;
          padding: 0.4rem;
          min-width: 3rem;
          min-height: 3rem;
          background: #2f2f2f;
          width: 3rem;
          height: 3rem;
          border-radius: 50rem;
          margin: 0 auto;
        }
      }
      .player-range {
        padding: 0.6rem 0.4rem;
        // border-bottom: 0.1rem solid #fff;
        .band {
          padding: 0.1rem 0;
          display: flex;
          align-items: center;
          justify-content: center;
          letter-spacing: 0.08rem;
          line-height: 1.3;
          border-radius: 15px;
          background: transparent !important;
          color: #fff !important;
          margin: 0;
          font-size: 1.1rem;
          font-family: $proxima-nova;
          font-weight: 600;
        }
      }
      .player-level {
        padding: 0.6rem 0.4rem;
        img {
          display: flex;
          max-width: 3rem;
          max-height: unset !important;
          -o-object-fit: contain;
          object-fit: contain;
          -o-object-position: center;
          object-position: center;
          padding: 0.2rem;
          width: 100%;
          height: 3rem;
          margin-bottom: 0;
        }
        h6 {
          font-family: $proxima-nova;
          font-weight: 600;
          font-size: 1rem;
          text-transform: uppercase;
          letter-spacing: 0.08rem;
          text-align: center;
          margin-bottom: 0;
          color: #fff;
        }
      }
    }
    img {
      position: relative;
      z-index: 1;
      min-width: 100%;
      width: 100%;
      -o-object-fit: contain;
      object-fit: contain;
      margin-bottom: 0;
      -o-object-position: top;
      object-position: top;
      border-radius: 0.5rem 0.5rem 0 0;
      height: auto;
    }
    .top-content-title {
      position: relative;
      padding: 1rem;
      .more-nft-title {
        color: #fff;
        font-family: $proxima-nova;
        font-weight: 700;
        letter-spacing: 0.08rem;
      }
      .more-nft-desc {
        color: #fff;
        font-family: $proxima-nova;
        font-weight: 400;
        letter-spacing: 0.08rem;
        white-space: nowrap;
        width: 100%;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .heart_box {
        position: absolute;
        z-index: 2;
        height: 3rem;
        width: 3rem;
        border-radius: 50rem;
        right: 1rem;
        top: -3rem;
        .svg_size {
          height: 3rem;
          width: 3rem;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 0.5rem;
          border: 0.1rem solid #fff;
          color: #fff !important;
          margin-bottom: 0.5rem;
          border-radius: 50rem;
          cursor: pointer;
          background-size: 1.4rem;
          background-position: center;
          background-repeat: no-repeat;

          &.heart_icon {
            border: 0.1rem solid #e8318d;
            box-shadow: inset 0px 0px 20px #e8318d !important;
            background-image: url("../../images/jump-trade/heart_ico.png");
          }
          &.filled_heart_icon {
            border: 0.1rem solid #e8318d;
            box-shadow: inset 0px 0px 20px #e8318d !important;
            background-image: url("../../images/jump-trade/heart-filled.png");
          }
        }
      }
    }

    .more-bid-details {
      margin-top: 1.5rem;
      .mb-title {
        color: #fff !important;
        font-family: $proxima-nova;
        font-weight: 400;
        font-size: 0.9rem;
        letter-spacing: 0.08rem;
      }
      .mb-value {
        color: #fff;
        font-family: $proxima-nova;
        font-weight: 700;
        font-size: 1rem;
      }
    }
    .item-post {
      position: relative;

      .nft-type-badge {
        color: #95ff8d;
        font-size: 0.7rem;
        border-radius: 0.3rem;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 1.5rem;
        max-width: 3.5rem;
        min-width: 3.5rem;
        padding: 0;
        box-shadow: none;
        z-index: 2;
        background: transparent;
        border: 0.05rem solid #95ff8d;
        font-family: $proxima-nova;
        font-weight: 600;
      }
    }
    .nft-type-badge-rank {
      position: absolute;
      top: 0.5rem;
      left: 0.5rem;
      right: auto;
      background: #000;
      border: 1px solid #fff;
      color: #fff;
      font-size: 1rem;
      border-radius: 0.3rem;
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 6rem;
      padding: 0.1rem 0.5rem;
      box-shadow: none;
      z-index: 2;
      font-family: $proxima-nova;
      font-weight: 600;

      .rank-title {
        font-size: 0.9rem;
        text-transform: capitalize;
        font-family: $proxima-nova;
        font-weight: 600;
        margin-right: 0;
        display: flex;
        align-items: center;
        svg {
          margin-right: 0.2rem;
        }
      }
    }

    .item-content {
      position: relative;
      color: #fff;
      padding: 1rem;
      z-index: 1;
    }
    .time-counter-box {
      position: absolute;
      left: 1rem;
      right: initial;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: none;
      background: #000;
      border-radius: 50rem;
      color: #fff;
      top: -3rem;
      flex-direction: row;
      background: transparent linear-gradient(94deg, #e2ff00 26%, #08ff0b 100%)
        0% 0% no-repeat padding-box;
      .time-counter-card {
        display: flex;
        align-items: flex-start;
        justify-content: center;
        flex-direction: column;
        position: relative;
        z-index: 1;
        color: #000;
        padding: 0.2rem 1rem 0.2rem 1rem;
        border-radius: 50rem;
        background: transparent
          linear-gradient(94deg, #e2ff00 26%, #08ff0b 100%) 0% 0% no-repeat
          padding-box;

        span.time-title,
        .nft-counter {
          line-height: 1;
        }
      }
    }
  }
  .theme-btn {
    // min-width: 12rem;
    border: none;
    box-shadow: none !important;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
    position: relative;

    padding: 0.5rem;
    font-size: 1.1rem;
    font-weight: 600;
    border-radius: 50rem;
    margin: 0.5rem auto;
    width: 100%;
    // max-width: 20rem;
    background: #de6f00;
    &:hover,
    &:focus,
    &:active {
      border: solid 1px #de6f00;
      background: transparent;
      color: #ffffff !important;
    }
    &.theme-btn-sec {
      min-width: 12rem;
      border: none;
      box-shadow: none !important;
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: 0.3s;
      position: relative;
      padding: 0.5rem;
      font-size: 1.1rem;
      border-radius: 50rem;
      margin: 0.5rem auto;
      width: 100%;
      max-width: 20rem;
      background: #4c4d5a;
      border: solid 1px transparent;
      border-radius: 50rem;
      font-family: $proxima-nova;
      font-weight: 600;
      &:hover,
      &:focus,
      &:active {
        border: solid 1px #4c4d5a;
        background: transparent;
        color: #ffffff !important;
      }
    }

    &.rounded-bordered {
      background: transparent;
      border: 0.1rem solid #4c4d5a;
      color: #ffffff !important;

      &:hover,
      &:focus,
      &:active,
      &.active {
        border: solid 1px transparent;
        background: #4c4d5a;

        color: #ffffff !important;
      }
      &.icon-btn {
        height: 2.3rem;
        width: 2.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        svg {
          height: 1.2rem;
          width: 1.2rem;
          color: #4c4d5a;
        }
        &:hover,
        &:focus,
        &:active {
          svg {
            color: #ffffff;
          }
        }
      }
    }

    &:disabled {
      background: #4c4d5a;
      color: #ffffff;
      border: none;
      opacity: 0.6;
    }
    span {
      position: relative;
      z-index: 2;
    }
  }
}

.nft-card-btn-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  button {
    max-width: unset !important;
    min-width: unset !important;
    width: 49% !important;
    @media screen and (max-width: 520px) {
      font-size: 1rem !important;
    }
  }
}
.yeild-confirm-popup {
  .modal-body {
    padding: 2rem;
    p {
      font-size: 1.2rem;
      margin: 0 !important;
    }
  }
  .modal-footer {
    flex-wrap: nowrap;
    button {
      svg {
        font-size: 1rem;
      }
      @media screen and (max-width: 540px) {
        max-width: unset;
        min-width: unset;
      }
    }
  }
}
